<template>
  <div class="Member">
    <el-card>
      <span>请输入项目名称 &emsp;</span>
      <el-input placeholder="请输入关键字" v-model="input" clearable style="width:200px" size="mini"></el-input>&emsp;
      <el-button type="text" class="btn btn-primary">搜索</el-button>
      <el-button
        type="text"
        class="btn btn-primary"
        style="float:right"
        @click.stop="addMember"
      >添加成员</el-button>
      <el-dialog
        title="添加成员"
        :visible.sync="dialogFormVisible"
        :append-to-body="true"
        :modal-append-to-body="false"
      >
        <el-form :model="form">
          <el-form-item label="选择部门" label-width="120px">
            <el-select v-model="selectValue1" filterable placeholder="请选择部门">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>&emsp;
            <span>选择团队</span>&nbsp;
            <el-select v-model="selectValue2" filterable placeholder="请选择团队">
              <el-option
                v-for="item in options2"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="团队成员"
            label-width="120px"
            v-for="( childMember ,item ) in form.member"
            :key="item"
          >
            <el-input v-model="childMember.name" style="width:218px"></el-input>&emsp;
            <span>成员岗位</span>&nbsp;
            <el-input v-model="childMember.position" style="width:218px" disabled></el-input>&emsp;
            <el-button
              type="primary"
              icon="el-icon-plus"
              circle
              size="mini"
              @click.stop="addMemberBtn"
            ></el-button>
            <el-button
              type="primary"
              icon="el-icon-minus"
              circle
              size="mini"
              @click.stop="deleteMemberBtn(childMember.name)"
            ></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="saveMember" style="margin-left:50px">保存</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
    <el-table :data="tableData" stripe style="width: 100%" :cell-style="{padding:0}">
      <el-table-column prop="id" label="ID" width="100"></el-table-column>
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column prop="account" label="域账号"></el-table-column>
      <el-table-column prop="projectCode" label="项目编码"></el-table-column>
      <el-table-column prop="IterationCode" label="迭代编码"></el-table-column>
      <el-table-column prop="inputRatio" label="投入比"></el-table-column>
      <el-table-column prop="characterID" label="角色ID"></el-table-column>
      <el-table-column prop="enterTime" label="进入项目时间"></el-table-column>
      <el-table-column prop="dropTime" label="退出项目时间"></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
            <el-button
              @click.stop="deleteMember(scope.row)"
              type="danger"
              size="mini"
              class="el-icon-delete"
            ></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <CutPage v-on:currentPage="currentPage" style="margin-top:20px"></CutPage>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectValue1: "",
      selectValue2: "",
      options: [
        {
          value: "选项1",
          label: "第一个部门"
        },
        {
          value: "选项2",
          label: "第二个部门"
        },
        {
          value: "选项3",
          label: "第三个部门"
        },
        {
          value: "选项4",
          label: "第四个部门"
        },
        {
          value: "选项5",
          label: "第五个部门"
        }
      ],
      options2: [
        {
          value: "选项1",
          label: "第一个团队"
        },
        {
          value: "选项2",
          label: "第二个团队"
        },
        {
          value: "选项3",
          label: "第三个团队"
        },
        {
          value: "选项4",
          label: "第四个团队"
        },
        {
          value: "选项5",
          label: "第五个团队"
        }
      ],
      dialogFormVisible: false,
      form: {
        name: "",
        member: [
          { name: "张三", position: "测试" },
          { name: "李四", position: "测试" },
          { name: "王五", position: "测试" }
        ]
      },
      input: "",
      tableData: [
        {
          id: "1",
          name: "张三",
          account: "lwx789456",
          projectCode: "123456789",
          IterationCode: "123456",
          inputRatio: "50%",
          characterID: "2334",
          enterTime: "2019-01-01",
          dropTime: "2020-02-02"
        },
        {
          id: "2",
          name: "李四",
          account: "lwx789456",
          projectCode: "123456789",
          IterationCode: "123456",
          inputRatio: "50%",
          characterID: "2334",
          enterTime: "2019-01-01",
          dropTime: "2020-02-02"
        },
        {
          id: "3",
          name: "王五",
          account: "lwx789456",
          projectCode: "123456789",
          IterationCode: "123456",
          inputRatio: "50%",
          characterID: "2334",
          enterTime: "2019-01-01",
          dropTime: "2020-02-02"
        },
        {
          id: "4",
          name: "赵六",
          account: "lwx789456",
          projectCode: "123456789",
          IterationCode: "123456",
          inputRatio: "50%",
          characterID: "2334",
          enterTime: "2019-01-01",
          dropTime: "2020-02-02"
        },
        {
          id: "5",
          name: "孙二狗",
          account: "lwx789456",
          projectCode: "123456789",
          IterationCode: "123456",
          inputRatio: "50%",
          characterID: "2334",
          enterTime: "2019-01-01",
          dropTime: "2020-02-02"
        },
        {
          id: "6",
          name: "哈哈哈",
          account: "lwx789456",
          projectCode: "123456789",
          IterationCode: "123456",
          inputRatio: "50%",
          characterID: "2334",
          enterTime: "2019-01-01",
          dropTime: "2020-02-02"
        }
      ]
    };
  },
  methods: {
    currentPage(value) {
      console.log(value);
    },
    addMember() {
      this.dialogFormVisible = true;
    },
    deleteMember(row) {
      this.$confirm("确认删除？")
        .then(_ => {
          for (let i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].id == row.id) {
              this.tableData.splice(i, 1);
            }
          }
        })
        .catch(_ => {});
    },
    addMemberBtn() {
      let item = { name: "", position: "" };
      this.form.member.push(item);
    },
    deleteMemberBtn(value) {
      for (let i = 0; i < this.form.member.length; i++) {
        if (this.form.member[i].name == value) {
          this.form.member.splice(i, 1);
        }
      }
    },
    saveMember() {
      this.dialogFormVisible = false;
      console.log(this.form);
    }
  }
};
</script>

<style lang="less" scoped>
